import React from "react";
import Card from "./Card";
import "./Detail.css";
import Rotate from "react-reveal/Rotate";
function Detail() {
  return (
    <div className="detail">
      <Rotate bottom right>
        <div>
          <Card
            title="Introduction"
            className="card"
            description="Engineering today is one of the most popular courses that professionally oriented students opt for. The opportunities in engineering are virtually limitless, allowing you to pursue a career in almost any field, in any industry, in any location around the world. With the ever-evolving requirements of the industry and the lucrative jobs that engineering offers to the graduates, students need to be well versed with the subjects of their field. "
          />
        </div>

        <div>
          <Card
            className="card"
            title="Why Success Tutor"
            description="At Success Tutor our focus is on knowledge, a fast track to career success. We have defined Engineering Classes, Group Coaching and Private Home Tutoring in Mumbai to instill confidence to our students, a Group Coaching is a beneficial advantage of peer learning from interaction, commonly known as a collective wisdom. Formation, insight and integration of thought are the basis for our Engineering Classes in Mumbai. At Wisdom Academy our Engineering Classes and Home Tutors are confident; "
            bg="#333333"
          />
        </div>
        <div>
          <Card
            className="card"
            title="Our Mission"
            description="At Success Tutor we nurture the students to explore their full potential so as to boost their self-confidence and moral in order to realize their dreams by advanced teaching methodology."
            bg="#fd7e14"
          />
        </div>
      </Rotate>
    </div>
  );
}

export default Detail;
